@keyframes fadeIn {
  from { opacity: 0; }
    to { opacity: 1; }
}

@keyframes fadeOut {
  from { opacity: 1; }
    to { opacity: 0; }
}

@keyframes showBody {
  from { transform: translateY(30%); }
    to { transform: translateY(0); }
}

@keyframes hideBody {
  from { transform: translateY(0); }
    to { transform: translateY(30%); }
}

.price-popup[aria-hidden="false"] {
  animation: fadeIn .3s cubic-bezier(0.0, 0.0, 0.2, 1);
}

.price-popup[aria-hidden="true"] {
  animation: fadeOut .3s cubic-bezier(0.0, 0.0, 0.2, 1);
}

.price-popup[aria-hidden="false"] .price-popup-body {
  animation: showBody .3s cubic-bezier(0.0, 0.0, 0.2, 1);
}

.price-popup[aria-hidden="true"] .price-popup-body {
  animation: hideBody .3s cubic-bezier(0.0, 0.0, 0.2, 1);
}

.price-popup {
  display: none;

  &__overlay {
    display: block;
    width: 100%;
    height: 100%;
    padding: 20px;
    background: rgba(0,0,0,0.6);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow-y: auto;
  }

  &.is-open {
    display: block;
  }
}

.price-popup-close {
  position: absolute;
  top: 30px;
  left: calc(50% + 305px);
  cursor: pointer;
}

.price-popup-body {
  max-width: 560px;
  margin: 65px auto;
  background-color: #fbfbf9;
}

.price-popup-body-img {
  img {
    max-width: 100%;
  }
}

.price-popup-body-title {
  margin-top: 40px;
  font-size: 32px;
  font-weight: 600;
  text-align: center;
}

.price-popup-body-list {
  padding: 15px 45px 40px;
  list-style-position: inside;
  font-weight: 300;
  line-height: 1.55;

  &__item {
    margin-bottom: 25px;
    font-weight: 700;
    text-transform: uppercase;
  }

  &__divider {
    margin: 35px 0;
    width: 220px;
    height: 1px;
    background-color: #121212;
  }
}

@media (max-width: 1200px) {
  .price-popup-body {
    max-width: 460px;
  }

  .price-popup-body-title {
    font-size: 30px;
  }
  
  .price-popup-body-list {
    font-size: 14px;
  
    &__item {
      margin-bottom: 25px;
      font-weight: 700;
      text-transform: uppercase;
    }
  
    &__divider {
      margin: 35px 0 30px;
      width: 190px;
    }
  }

  .price-popup-close {
    left: calc(50% + 260px);
  }
}

@media (max-width: 960px) {
  .price-popup-body-title {
    margin-top: 20px;
  }

  .price-popup-body-list {
    padding: 15px 30px 30px;
  }
}

@media (max-width: 640px) {
  .price-popup-body-title {
    font-size: 26px;
  }

  .price-popup-body-list {
    padding: 10px 20px 20px;
    font-size: 12px;

    &__item {
      margin-bottom: 10px;
    }
  
    &__divider {
      margin: 30px 0 20px;
      width: 165px;
    }
  }

  .price-popup-close {
    top: 40px;
    left: auto;
    right: 30px;
  }
}