@import './variables.scss';
@import './animations.scss';

*,
*::before,
*::after {
	box-sizing: border-box;
	padding: 0;
	margin: 0;
}

html {
	font-family: 'Roboto', Arial, sans-serif;
	color: #121212;
}

body {
	position: relative;
}

.bold {
	font-weight: 700;
}

.button-animation {
	width: 100%;
	height: 100%;
	transform: translateX(-85px);
	animation: flash 3s linear infinite;
	position: absolute;
	top: 0;
	left: 0;

	&__element {
		width: 45px;
		height: 100%;
		background: linear-gradient(90deg, rgba(255, 255, 255, .1), rgba(255, 255, 255, .4));
		transform: skewX(-45deg);
		position: absolute;
		top: 0;
		left: 30px;
	}
}

.progress {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 4px;
}

.progress-bar {
	height: 100%;
	background-color: rgb(35, 131, 179);
	transition: all ease 0.5s;
}

.scroll-button {
	opacity: 0;
	visibility: hidden;
	position: fixed;
	left: 30px;
	bottom: 30px;
	cursor: pointer;
	transition: all ease 0.2s;

	&.active {
		opacity: 1;
		visibility: visible;
	}
}

@media (max-width: 640px) {
	.scroll-button {
		left: 10px;
		bottom: 10px;
	}
}


