.about {
	padding: 120px 20px 75px;
}

.about-text {
	max-width: 620px;
	margin: 0 auto 90px;
	text-align: center;

		&__title {
			font-size: 42px;
			font-weight: 600;
		}
}

.about-list {
	max-width: 760px;
	margin: 0 auto 90px;
}

.about-list-item {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: flex-start;
	margin-bottom: 50px;

		&__icon {
			margin-right: 40px;

			svg {
				width: 55px;
				height: 55px;
			}
		}

		&__text {
			font-size: 20px;
			font-weight: 300;
			line-height: 1.5;
		}

		&:last-child {
			margin-bottom: 0;
		}
}

.about-description {
	text-align: center;

	&__title {
		margin-bottom: 30px;
		font-size: 24px;
		font-weight: 600;
	}

	&__subtitle {
		font-size: 20px;
		font-weight: 300;
	}
}


@media (max-width: 1200px) {
	.about-text {
			&__title {
				font-size: 38px;
			}
	}

	.about-list {
		max-width: 620px;
	}

	.about-description {
		&__title {
			font-size: 22px;
		}
	}
}

@media (max-width: 960px) {
	.about-text {
		margin-bottom: 45px;
	}

	.about-list-item {
		&__icon {
			margin-right: 20px;

			svg {
				width: 50px;
				height: 50px;
			}
		}
	}

	.about-description {
		max-width: 600px;
		margin: 0 auto;
	}
}

@media (max-width: 640px) {
	.about-text {
			&__title {
				font-size: 30px;
			}
	}

	.about-list-item {
		&__icon svg {
			width: 40px;
			height: 40px;
		}
	}

	.about-description {
		&__title {
			font-size: 20px;
		}
	}
}