@keyframes flash {
	30% { transform: translateX(100%); }
	100% { transform: translateX(100%); }
}

@keyframes appear {
	0% { opacity: 0; }
	100% { opacity: 1; }
}

@keyframes zoomIn {
	0% {
		opacity: 0;
		transform: scale(0.9);
	}
	100% {
		opacity: 1;
		transform: scale(1);
	}
}

@keyframes arrow {
	0% { transform: translateY(-5px); }
	50% { transform: translateY(5px); }
	100% { transform: translateY(-5px); }
}