.faq {
	padding: 135px 0 90px;
}

.faq-text {
	text-align: center;
	margin-bottom: 90px;

		&__title {
			font-size: 42px;
			font-weight: 600;
		}
}

.faq-tabs {
	max-width: 760px;
	margin: 0 auto 100px;
}

.faq-tab {
	border-top: 1px solid #EEEEEE;

	&:last-child {
		border-bottom: 1px solid #EEEEEE;
	}
}

.faq-tab-head {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: space-between;
	padding: 20px 0;
	cursor: pointer;

		&__title {
			font-size: 20px;
			font-weight: 600;
		}

		&__toggle {
			width: 40px;
			height: 40px;
			flex-shrink: 0;
			border-radius: 50%;
			position: relative;
			transition: all ease-in-out 0.2s;

			&--1 {
				width: 24px;
				height: 2px;
				background-color: #000;
				position: absolute;
				top: calc(50% - 1px);
				left: calc(50% - 12px);
				transition: all ease-in-out 0.2s;
			}

			&--2 {
				width: 2px;
				height: 24px;
				background-color: #000000;
				position: absolute;
				top: calc(50% - 12px);
				left: calc(50% - 1px);
				transition: all ease-in-out 0.2s;
			}

			&.active {
				transform: rotate(-45deg);
			}
		}

		&:hover {
			.faq-tab-head__toggle {
				background-color: #2b75a8;

				.faq-tab-head__toggle--1 {
					background-color: #FFFFFF;
				}
	
				.faq-tab-head__toggle--2 {
					background-color: #FFFFFF;
				}
			}
		}
}

.faq-tab-body {
	display: none;
	font-size: 20px;
	font-weight: 300;
	line-height: 1.55;
	overflow: hidden;
	transition: all ease-in-out 0.3s;

	div {
		padding-bottom: 40px;
	}

	ul {
		margin-top: -15px;
		padding-left: 20px;
		padding-bottom: 40px;
	}
}

.faq-divider {
	max-width: 1160px;
	height: 1px;
	margin: 0 auto 115px;
	background-color: #000000;
	opacity: 0.2;
}

.faq-video {
	text-align: center;

	iframe {
		width: 760px;
		height: 430px;
	}
}

@media (max-width: 1200px) {
	.faq-text {
		&__title {
			font-size: 38px;
		}
	}

	.faq-tabs {
		max-width: 620px;
	}

	.faq-divider {
		max-width: 940px;
	}

	.faq-video {
		iframe {
			width: 620px;
			height: 350px;
		}
	}
}

@media (max-width: 980px) {
	.faq-tabs {
		margin-bottom: 165px;
	}

	.faq-divider {
		display: none;
	}
}

@media (max-width: 640px) {
	.faq-text {
		margin-bottom: 45px;

		&__title {
			font-size: 30px;
		}
	}

	.faq-tabs {
		padding: 0 20px;
	}

	.faq-tab-head {
		padding: 10px 0;
	}

	.faq-video {
		iframe {
			width: 100%;
			height: 350px;
		}
	}
}