.contact-popup-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  font-size: 0;
  border-radius: 50%;
  box-shadow: 0 0 10px 0 rgb(0 0 0 / 30%);
  background-color: #12abde;
  position: fixed;
  right: 20px;
  bottom: 20px;
  cursor: pointer;
  transition: all linear 0.2s;

  &__icon {
    transition: scale(1);
  }

  &__icon--close {
    display: none;
  }

  &.active {
    background-color: #FFFFFF;

    .contact-popup-button__icon {
      display: none;
    }

    .contact-popup-button__icon--close {
      display: block;
    }
  }

  &.not-clicked {
    animation: contact-popup-button 2s ease-out infinite;
  }
}

@keyframes contact-popup-button {
  0% { transform:rotate(0deg) scale(1) }
  25% { transform:rotate(10deg) scale(1.1) }
  50% { transform:rotate(0deg) scale(1) }
  70% { transform:rotate(-10deg) scale(1.1) }
  100% { transform:rotate(0deg) scale(1) }
}

.contact-popup {
  display: none;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
  max-width: 360px;
  padding: 30px 35px 40px;
  text-align: center;
  border-radius: 5px;
  background-color: #FFFFFF;
  box-shadow: 0 0 20px 0 rgb(0 0 0 / 40%);
  position: fixed;
  right: 30px;
  bottom: 105px;
  opacity: 0;
  transition: all ease 0.2s;

  &__title {
    margin-bottom: 25px;
    font-size: 24px;
    font-weight: 600;
    line-height: 1.35;
    color: #2383b3;
  }

  &__call {
    margin-bottom: 5px;
    font-size: 16px;
    font-weight: 300;
  }

  &__phone {
    font-size: 24px;
    font-weight: 600;
    line-height: 1.55;
    color: #121212;
  }

  &.active {
    opacity: 1;
  }
}

.contact-popup-form {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin-bottom: 30px;

  input {
    width: 100%;
    height: 50px;
    padding: 0 20px;
    margin-bottom: 10px;
    font: inherit;
    font-size: 16px;
    border: 1px solid #d1d1d1;
  }

  button {
    width: 100%;
    height: 50px;
    background-color: #2383b3;
    font: inherit;
    font-size: 16px;
    font-weight: 700;
    border: none;
    color: #FFFFFF;
    text-transform: uppercase;
    cursor: pointer;
  }

  &--success {
    display: none;
    margin: 20px 0 50px;
    opacity: 0;
    font-size: 18px;
    font-weight: 300;
    transition: all ease 0.2s;
  }
}

@media (max-width: 960px) {
  .contact-popup-button {
    right: 10px;
    bottom: 10px;
  }
}