.benefits {
	padding: 120px 0;
}

.benefits-text {
	margin-bottom: 105px;
	text-align: center;

		&__title {
			font-size: 42px;
			font-weight: 600;
		}
}

.benefits-cards {
	display: flex;
	flex-flow: row wrap;
	align-items: flex-start;
	justify-content: space-between;
	max-width: 1200px;
	margin: 0 auto;
}

.benefits-card {
	flex: 0 0 30%;
	margin-bottom: 70px;

		&__img {
			width: 55px;
			height: 55px;
			margin-bottom: 20px;
		}

		&__title {
			font-size: 23px;
			font-weight: 600;
			margin-bottom: 20px;
		}

		&__subtitle {
			font-size: 20px;
			font-weight: 300;
			line-height: 1.5;
		}

		&:nth-child(4),
		&:nth-child(5),
		&:nth-child(6) {
			margin-bottom: 0;
		}
}

@media (max-width: 1200px) {
	.benefits-text {
		&__title {
			font-size: 38px;
		}
	}

	.benefits-cards {
		max-width: 960px;
	}

	.benefits-card {
		&__title {
			font-size: 18px;
		}
	}
}

@media (max-width: 960px) {
	.benefits-text {
		margin-bottom: 45px;
	}

	.benefits-cards {
		flex-direction: column;
		max-width: 600px;
	}

	.benefits-card {
		margin-bottom: 30px;

		&__title {
			font-size: 18px;
		}
	}
}

@media (max-width: 640px) {
	.benefits-text {
		&__title {
			font-size: 30px;
		}
	}

	.benefits-cards {
		padding: 0 20px;
	}

	.benefits-card {
		&__title {
			font-size: 16px;
		}
	}
}