.contact {
  padding: 185px 0;
	background: linear-gradient(to top, rgba(47,110,161,0.90), rgba(37,131,182,0.80)), center / cover no-repeat url('../assets/contact.jpg');
	color: #FFFFFF;
}

.contact-text {
  margin-bottom: 75px;
  text-align: center;

		&__title {
      margin-bottom: 25px;
      font-size: 48px;
      font-weight: 600;
		}

		&__subtitle {
      font-size: 20px;
      font-weight: 300;
		}
}

.contact-form {
  max-width: 980px;
  margin: 0 auto;

  input {
    width: 355px;
    height: 56px;
    margin-right: 20px;
    padding: 0 20px;
    font: inherit;
    font-size: 16px;
    border: none;
  }

  button {
    height: 56px;
    padding: 0 40px;
    color: #FFFFFF;
    font: inherit;
    font-size: 16px;
    font-weight: 700;
    border: 2px solid #FFFFFF;
    border-radius: 6px;
    background-color: #12abde;
    position: relative;
    overflow: hidden;
    transition: all ease-in-out 0.2s;
    cursor: pointer;

    &:hover {
      background-color: #0bb4e6;
    }
  }

  &--success {
    display: none;
    width: 100%;
    padding: 20px;
    font-size: 20px;
    font-weight: 300;
    line-height: 1.55;
    text-align: center;
    color: #2383b3;
    background-color: #FFFFFF;
    opacity: 0;
    transition: all ease 0.2s;
  }
}

@media (max-width: 1200px) {
  .contact-text {
    &__title {
      font-size: 44px;
    }
  }

  .contact-form {
    max-width: 790px;

    input {
      width: 265px;
    }

    &--success {
      font-size: 18px;
    }
  }
}

@media (max-width: 960px) {
  .contact {
    padding: 108px 0;
  }

  .contact-form {
    display: flex;
    flex-flow: column nowrap;
    align-items: flex-start;
    justify-content: flex-start;
    max-width: 640px;

    input {
      width: 100%;
      margin: 0 0 20px 0;
    }

    button {
      width: 100%;
    }
  }
}

@media (max-width: 640px) {
  .contact {
    padding: 108px 0;
  }

  .contact-text {
    margin-bottom: 35px;

      &__title {
        font-size: 30px;
      }
  }

  .contact-form {
    padding: 0 20px;

    input {
      height: 50px;
    }

    button {
      height: 50px;
    }

    &--success {
      font-size: 16px;
    }
  }
}