.call {
	padding: 120px 0 75px;
	text-align: center;
}

.call-text-card {
	display: inline-block;
	padding: 60px 120px;
	margin-bottom: 45px;
	border: 6px solid #2b75a8;

		&__title {
			font-size: 42px;
			font-weight: 600;
			color: #2b75a8;
		}
}

.call-text {
	text-align: center;
	color: #2b75a8;

		&__title {
			font-size: 26px;
			font-weight: 700;
		}

		&__phone {
			margin: 20px 0 5px;
			font-size: 42px;
			font-weight: 700;
			text-decoration: none;
			color: inherit;
		}

		&__subtitle {
			margin-bottom: 45px;
			font-size: 20px;
			font-weight: 300;
		}

		&__button {
			display: inline-flex;
			align-items: center;
			justify-content: center;
			height: 64px;
			padding: 0 60px;
			font-size: 16px;
			font-weight: 700;
			color: #2b75a8;
			text-decoration: none;
			border: 2px solid #2b75a8;
			border-radius: 6px;
			position: relative;
			overflow: hidden;
			transition: all ease-in-out 0.2s;

			&:hover {
				color: #3393d6;
				border-color: #3393d6;
			}
		}
}

@media (max-width: 1200px) {
	.call-text-card {
		padding: 40px;
	}
}

@media (max-width: 700px) {
	.call-text-card {
		padding: 20px;
	}
}

@media (max-width: 480px) {
	.call {
		padding: 90px 0 75px;
	}

	.call-text {
		text-align: center;
		color: #2b75a8;
	
			&__title {
				font-size: 22px;
			}
	
			&__phone {
				margin: 10px 0 5px;
				font-size: 30px;
			}
	
			&__subtitle {
				font-size: 18px;
			}
	
			&__button {
				padding: 0 30px;
			}
	}
}