.difference {
	padding: 60px 0 70px;
}

.difference-text {
	margin-bottom: 60px;
	text-align: center;

		&__title {
			font-size: 42px;
			font-weight: 600;
		}
}

.difference-gallery {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: space-between;
	max-width: 1160px;
	margin: 0 auto 85px;
}

// .difference-gallery-item {
// 		&__img {
// 		}
// }

.difference-divider {
	max-width: 1160px;
	height: 1px;
	margin: 0 auto 105px;
	background-color: #000000;
	opacity: 0.2;
}

.difference-instruments {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: space-between;
	max-width: 960px;
	margin: 0 auto;
}

.difference-instruments-text {
	flex: 1 0 auto;
	margin-right: 40px;

		&__title {
			margin-bottom: 40px;
			font-size: 42px;
			font-weight: 700;
		}
}

.difference-instruments-text-list {
	padding-left: 20px;
	font-size: 22px;
	font-weight: 300;

		&__item {
			margin-bottom: 8px;
		}
}

.difference-instruments-img img{
	width: 460px;
	height: 360px;
}

@media (max-width: 1200px) {
	.difference-gallery {
		max-width: 960px;
	}

	.difference-gallery-item {
		&__img {
			height: 300px;
		}
	}

	.difference-divider {
		max-width: 940px;
	}
}

@media (max-width: 980px) {
	.difference-gallery {
		margin-bottom: 130px;
	}

	.difference-divider {
		display: none;
	}
}

@media (max-width: 960px) {
	.difference-gallery {
		flex-direction: column;
		margin-bottom: 110px;
	}

	.difference-gallery-item {
		margin-bottom: 20px;

		&:last-child {
			margin-bottom: 0;
		}

		img {
			height: auto;
		}
	}

	.difference-instruments {
		flex-flow: column;
	}

	.difference-instruments-text {
		margin: 0 0 30px 0;
	}
}

@media (max-width: 620px) {
	.difference-instruments-img img{
		max-width: 100%;
		width: auto;
		height: auto;
	}
}