.hero {
	display: flex;
	flex-flow: column nowrap;
	height: 100vh;
	background: linear-gradient(to top, rgba(12, 55, 79, 0.8), rgba(18, 171, 222, 0.7)), center / cover no-repeat url('../assets/hero.jpg');
	color: #FFFFFF;
}

.hero-header {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: space-between;
	padding: 60px 60px 0;
}

// .hero-header-logo {
// 		&__img {}
// }

.hero-header-phone {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: flex-start;

		&__icon {
			padding-right: 10px;
		}

		&__number {
			font-size: 24px;
			font-weight: 700;
			color: #FFFFFF;
			text-decoration: none;
		}
}

.hero-body {
	margin: auto;
}

.hero-body-text {
	max-width: 810px;
	margin: 0 auto;
	text-align: center;

		&__title {
			margin-bottom: 40px;
			font-size: 62px;
			font-weight: 600;
			letter-spacing: 1px;
			line-height: 80px;
			opacity: 0;
			animation: zoomIn 1.2s cubic-bezier(.19,1,.22,1) 0.7s 1 forwards;
		}

		&__subtitle {
			margin-bottom: 60px;
			font-size: 28px;
			font-weight: 400;
			opacity: 0;
			animation: zoomIn 0.7s cubic-bezier(.19,1,.22,1) 1.2s 1 forwards;
		}

		&__button {
			display: flex;
			align-items: center;
			justify-content: center;
			height: 60px;
			width: 336px;
			margin: 0 auto;
			border-radius: 21px;
			border: 2px solid #FFFFFF;
			background-color: rgb(42, 96, 166);
			color: #FFFFFF;
			text-decoration: none;
			transition: all ease-in-out 0.2s;
			position: relative;
			overflow: hidden;
			opacity: 0;
			animation: appear 0.2s ease-in-out 1.7s 1 forwards;

			&:hover {
				background-color: #3090d9;
			}
		}
}

.hero-footer {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: center;
	margin: auto 0 50px;
}

.hero-footer-arrow {
	animation: arrow 3s ease infinite;
}

@media (max-width: 980px) {
	.hero-header {
		opacity: 0;
		pointer-events: none;
		user-select: none;
	}
}