.price {
	padding: 105px 0 60px;
	background-color: #eeeeee;
}

.price-text {
	margin-bottom: 105px;
	text-align: center;

		&__title {
			font-size: 42px;
			font-weight: 600;
		}
}

.price-cards {
	display: flex;
	flex-flow: row wrap;
	align-items: center;
	justify-content: space-between;
	max-width: 1200px;
	margin: 0 auto 105px;
}

.price-card {
	text-align: center;
	margin-bottom: 60px;

		&__img {
			width: 360px;
			height: 240px;
			margin-bottom: 25px;
		}

		&__title {
			margin-bottom: 15px;
			font-size: 26px;
			font-weight: 600;
		}

		&__price {
			margin-bottom: 25px;
			font-size: 20px;
			font-weight: 300;
		}

		&__button {
			display: inline-flex;
			align-items: center;
			justify-content: center;
			width: 250px;
			height: 45px;
			text-decoration: none;
			font-size: 14px;
			font-weight: 700;
			color: #121212;
			background-color: #FFFFFF;
			border: 2px solid #2b75a8;
			border-radius: 6px;
			transition: all ease-in-out 0.2s;

			&:hover {
				color: #2b75a8;
				border-color: #1f8ec1;
			}
		}

		&:nth-child(4),
		&:nth-child(5),
		&:nth-child(6) {
			margin-bottom: 0;
		}
}

.price-pricelist {
	text-align: center;
}

.pricelist-button {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	height: 66px;
	width: 350px;
	font-size: 16px;
	font-weight: 700;
	background: none;
	border: 3px solid #000000;
	border-radius: 6px;
	transition: all ease-in-out 0.2s;
	cursor: pointer;
	position: relative;
	overflow: hidden;

	&:hover {
		color: #2b75a8;
		border-color: #2b75a8;
	}
}

@media (max-width: 1200px) {
	.price-cards {
		max-width: 960px;
	}

	.price-card {
			&__img {
				width: 300px;
				height: 200px;
			}

			&__title {
				font-size: 24px;
			}
	}
}

@media (max-width: 960px) {
	.price-text {
		margin-bottom: 30px;
	
			&__title {
				font-size: 38px;
			}
	}

	.price-cards {
		max-width: 600px;
	}

	.price-card {
			&__img {
				width: 100%;
				height: auto;
			}

			&__title {
				font-size: 24px;
			}

			&:nth-child(4),
			&:nth-child(5) {
				margin-bottom: 60px;
			}
	}
}