.reviews {
	padding: 120px 0 60px;
	background-color: #EEEEEE;
}

.reviews-text {
	text-align: center;
	margin-bottom: 90px;

		&__title {
			font-size: 42px;
			font-weight: 600;
		}
}

.reviews-cards {
	max-width: 760px;
	margin: 0 auto 75px;
}

.reviews-card {
	display: flex;
	flex-flow: row nowrap;
	align-items: flex-start;
	margin-bottom: 60px;

	&:nth-child(even) {
		flex-direction: row-reverse;

		.reviews-card-text {
			margin: 0 35px 0 0;
		}

		.reviews-card-text::before {
			left: auto;
			right: -24px;
			border-color: #FFFFFF transparent transparent #FFFFFF;
		}
	}

	&:last-child {
		margin-bottom: 0;
	}
}

.reviews-card-img {
	width: 100px;
	height: 100px;
	flex-shrink: 0;
	border-radius: 50%;
	overflow: hidden;

	img {
		width: inherit;
		height: inherit;
		object-fit: contain;
	}
}

.reviews-card-text {
	padding: 50px 35px 30px;
	margin-left: 35px;
	border-radius: 10px;
	background-color: #FFFFFF;
	line-height: 1.55;
	position: relative;

		&__title {
			margin-bottom: 10px;
			font-size: 20px;
			font-weight: 600;
		}

		&__subtitle {
			font-size: 20px;
			font-weight: 300;
		}

		&::before {
			content: "";
			display: block;
			position: absolute;
			top: 50px;
			left: -24px;
			border: 12px solid;
			border-color: #FFFFFF #FFFFFF transparent transparent;
		}
}

.reviews-link {
	text-align: center;

	&__button {
		display: inline-flex;
		align-items: center;
		justify-content: center;
		height: 66px;
		width: 320px;
		font-size: 16px;
		font-weight: 700;
		text-decoration: none;
		color: #000000;
		background: none;
		border: 3px solid #000000;
		border-radius: 6px;
		transition: all ease-in-out 0.2s;
		cursor: pointer;
		position: relative;
		overflow: hidden;
	
		&:hover {
			color: #2b75a8;
			border-color: #2b75a8;
		}
	}
}

@media (max-width: 1200px) {
	.reviews-text {
		&__title {
			font-size: 38px;
		}
	}

	.reviews-cards {
		max-width: 620px;
	}
}

@media (max-width: 960px) {
	.reviews-text {
		margin-bottom: 40px;
	}

	.reviews-card {
		margin-bottom: 40px;
	}
}

@media (max-width: 640px) {
	.reviews-text {
		&__title {
			font-size: 30px;
		}
	}

	.reviews-cards {
		padding: 0 20px;
	}

	.reviews-card-img {
		width: 40px;
		height: 40px;
	}

	.reviews-card:nth-child(even) {
		.reviews-card-text {
			margin: 0 15px 0 0;
		}

		.reviews-card-text::before {
			left: auto;
			right: -10px;
		}
	}

	.reviews-card-text {
		padding: 40px 20px 20px;
		margin-left: 15px;

		&::before {
			top: 20px;
			left: -10px;
		}
	}
	
}