.footer {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: space-between;
	padding: 12px 40px;
	background-color: $footerBackgroundColor;
}

.footer-company {
	flex: 1 1 30%;
	font-size: 14px;
}

.footer-socials {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: center;
	flex: 1 1 30%;
}

.footer-socials-item {
	&--vk {
		margin-right: 2px;
	}

	// &--instagram {
	// }
}

.footer-scroll {
	display: flex;
	flex-flow: row nowrap;
	align-items: flex-start;
	justify-content: flex-end;
	flex: 1 1 30%;
}

.footer-scroll-body {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: center;
	font-size: 14px;
	font-weight: 700;
	border: none;
	background-color: none;
	cursor: pointer;
	user-select: none;

		&__text {
			margin-right: 10px;
		}

		// &__icon {
		// }
}

@media (max-width: 1200px) {
	.footer {
		padding: 12px 20px;
	}
}

@media (max-width: 980px) {
	.footer {
		flex-flow: column nowrap;
		justify-content: center;
		padding: 40px 20px;
	}

	.footer-company {
		margin-bottom: 20px;
		order: 2;
		flex: 1;
		text-align: center;
	}
	
	.footer-socials {
		margin-bottom: 20px;
		order: 1;
		flex: 1;
	}

	.footer-scroll {
		order: 3;
		flex: 1;
	}
}

@media (max-width: 668px) {
	.footer-company {
		font-size: 12px;
	}

	.footer-scroll-body {
		font-size: 12px;
	}
}