.time {
	padding: 135px 0;
	background: linear-gradient(to bottom, rgba(17,171,222,0.80), rgba(35,131,179,0.90)), center / cover no-repeat url('../assets/time.webp');
	color: #FFFFFF;
}

.time-conatiner {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: space-between;
	max-width: 1200px;
	margin: 0 auto;
}

.time-card {
	max-width: 560px;
	text-align: center;

		&__title {
			margin-bottom: 25px;
			font-size: 72px;
			font-weight: 600;
		}

		&__subtitle {
			font-size: 20px;
			font-weight: 300;
			line-height: 1.5;
		}
}

@media (max-width: 1200px) {
	.time {
		padding: 195px 0;
	}

	.time-conatiner {
		max-width: 960px;
		flex-flow: column nowrap;
	}

	.time-card {
		max-width: 460px;

		&__title {
			font-size: 68px;
		}

		&:nth-child(1) {
			margin-bottom: 40px;
		}
	}
}

@media (max-width: 960px) {
	.time {
		padding: 85px 0;
	}

	.time-conatiner {
		max-width: 520px;
	}

	.time-card {
		max-width: 100%;
	}
}


@media (max-width: 640px) {
	.time {
		padding: 85px 20px;
	}

	.time-conatiner {
		max-width: 100%;
	}

	.time-card {
		&__title {
			font-size: 32px;
		}
	}
}